<template>
  <li
    class="tree-row"
    :style="{
      'padding-left': indentSize + 'px',
      'gap': gap + 'px',
      '--row-hover-background': rowHoverBackground
    }"
  >

    <div
      class="tree-row-item"
      @click.stop="toggleExpanded(node)"
    >
      <template v-if="node.nodes && node.nodes.length > 0 && useIcon">
        <template v-if="!node.expanded">
          <div class="arrow-block">
          <slot name="iconActive" style="display: block!important;">
            <img src="@/assets/newImages/structure_arrow_right.png" style="width: 12px; height: 17px; margin-bottom: 8px;">
          </slot>
          <!-- <div class="dashed-line" v-if="node.nodes && node.nodes.length > 0 && !isRoot"></div> -->
          </div>
        </template>
        <template v-else>
          <div class="arrow-block">
          <slot name="iconInactive">
            <img src="@/assets/newImages/structure_arrow_down.png" style="width: 17px; height: 12px; margin-bottom: 8px;">
          </slot>
          <!-- <div class="dashed-line" v-if="node.nodes && node.nodes.length > 0 && !isRoot"></div> -->
          </div>
        </template>
      </template>
      <!-- <div class="tree-row-item-header">
      </div> -->
      <div class="window tree-row-item-content" :class="node.level>node.rootStars ? 'light' : 'dark'">
        <!-- <button class="sell-referral-button" v-if="node.level===1" @click="sellReferral(node)">{{$localizeFilter('sell')}}</button> -->
        <p class="email">{{ node.email }}</p>
        <p class="name">{{node.name}}</p>
        <p class="telegram">{{ 'Telegram: @' + (node.telegramName || '') }}</p>
        <!-- <div class="status">
          <label>{{getStatus(node.stars)}}</label>
           <star-rating
            :max-rating="10"
            :show-rating="false"
            :star-size="10"
            :active-color="'#FF9400'"
            :inactive-color="node.level>node.rootStars ? '#333C41' : '#4E7675'"
            :border-color="'#000000'"
            :border-width="2"
            :active-border-color="'#FF9400'"
            :padding="2"
            :read-only="true"
            :rating="node.stars"/>
        </div> -->
      </div>
    </div>
    <ul
      v-if="node.expanded"
      :style="{'gap': gap + 'px'}"
      class="tree-list"
    >
      <tree-row
        v-for="child in node.nodes"
        :ref="'tree-row-' + child.id"
        :key="child.id"
        :node="child"
        :use-icon="useIcon"
        :gap="gap"
        :expand-row-by-default="expandRowByDefault"
        :indent-size="indentSize"
        :row-hover-background="rowHoverBackground"
        :setNode="setNode"
        :getNode="getNode"
        :updateNode="updateNode"
        :expandable="expandable"
        @emitNodeExpanded="emitNodeExpanded"
        @emitOnUpdated="emitOnUpdated"
      >
        <template #iconActive>
          <slot name="iconActive">
            <img src="@/assets/newImages/structure_arrow_right.png" style="width: 12px; height: 17px; margin-bottom: 8px;">
          </slot>
        </template>
        <template #iconInactive>
          <slot name="iconInactive">
            <img src="@/assets/newImages/structure_arrow_down.png" style="width: 17px; height: 12px; margin-bottom: 8px;">
          </slot>
        </template>
      </tree-row>
    </ul>
  </li>
</template>

<script>
import { nextTick, watch, onUpdated } from 'vue'
// import ArrowRight from './Icons/ArrowRight.vue'
// import ArrowDown from './Icons/ArrowDown.vue'
import StarRating from 'vue-star-rating'
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
  components: {
    // ArrowRight,
    // ArrowDown,
    StarRating
  },
  props: {
    node: {
      type: Object,
      required: true,
    },
    isRoot: {
      type: Boolean,
      default: false
    },
    indentSize: {
      type: Number,
      required: true,
    },
    gap: {
      type: Number,
      required: true,
    },
    getNode: {
      type: Function,
      required: true,
    },
    setNode: {
      type: Function,
      required: true,
    },
    updateNode: {
      type: Function,
      required: true,
    },
    expandRowByDefault: {
      type: Boolean,
      default: false,
    },
    useIcon:{
      type: Boolean,
      default: true,
    },
    rowHoverBackground: {
      type: String,
      default: '#e0e0e0',
    },
    expandable: {
      type: Boolean,
      default: true,
    },
  },
  emits: ['emitNodeExpanded', 'emitOnUpdated'],
  setup(props, { emit }) {
    // console.log(props)
    const toggleExpanded = node => {
      if (props.node.nodes == null || props.node.nodes.length == 0) {return}
      if (props.expandable) {
        props.node.expanded = props.node.nodes ? !props.node.expanded : false
        nextTick(() => {
          emit('emitNodeExpanded', node, props.node.expanded)
        })
      }
    }

    watch(() => props.expandRowByDefault, newVal => {
      // if (props.node.nodes == null || props.node.nodes.length == 0) {return}
      if (props.node.nodes) {
        props.node.expanded = !props.expandable
      }
    }, {
      immediate: true,
    })

    // redirect the event toward the Tree component
    const emitNodeExpanded = (node, state) => {
      emit('emitNodeExpanded', node, state)
    }

    const emitOnUpdated = (node, state) => {
      emit('emitOnUpdated', node, state)
    }

    onUpdated(() => {
      emitOnUpdated()
    })

    return {
      toggleExpanded,
      emitNodeExpanded,
      emitOnUpdated
    }
  },
  methods: {
    getStatus(stars) {
      return localizeFilter('marketing_status') + ' ' + stars
    },
    async sellReferral(node) {
     const data = JSON.parse(JSON.stringify(node))
     await this.$store.dispatch('sellReferral', {data})
    }
  }
})
</script>

